<template>
  <div>
    <el-row>
      <el-col :span="12">
        <!--默认配置的vue-ueditor-->
        <VueUEditor @ready="editorReady" :ueditorConfig="editorConfig2"></VueUEditor>
      </el-col>
      <el-col :span="12">
        <!--使用特殊配置的vue-ueditor，会覆盖ueditor.config.js里的配置-->
        <VueUEditor :ueditorConfig="editorConfig" @ready="editorReady2"></VueUEditor>
      </el-col>
    </el-row>
  </div>
</template>

<script>
import { getApis } from '@/module/flow/service/service'
import VueUEditor from 'vue-ueditor'

export default {
  name: 'test',
  components: {VueUEditor},
  data () {
    return {
      editorData: undefined,
      editorConfig: {
        retainOnlyLabelPasted: true,
        enableAutoSave: false,
        lang: 'en',
        fullscreen: false // 全屏
      },
      editorConfig2: {
        enableAutoSave: true,
        toolbars: [[
          'source',
          'simpleupload', // 单图上传
          'insertimage', // 多图上传]
          'attachment', // 附件
          'scrawl', // 涂鸦
          'wordimage', // 图片转存
          'insertvideo'// 上传你视频
        ]]
      }
    }
  },
  methods: {
    // eg:
    editorReady (editorInstance) {
      getApis().then(data => {
        editorInstance.setContent(data.data)
      })
      editorInstance.execCommand('italic')
      editorInstance.setOpt('initContent', '欢迎使用编辑器')

      editorInstance.addListener('contentChange', () => {
        console.log('编辑器内容发生了变化:', editorInstance.getContent())
      })
    },
    editorReady2 (editorInstance) {
      editorInstance.setContent('<img width="530" height="340" src="http://api.map.baidu.com/staticimage?center=116.404,39.915&zoom=10&width=530&height=340&markers=116.404,39.915"/>')
      editorInstance.execCommand('italic')
      editorInstance.setOpt('initContent', '欢迎使用编辑器')

      editorInstance.addListener('contentChange', () => {
        console.log('编辑器内容发生了变化:', editorInstance.getContent())
      })
    }
  }
}
</script>



