<template>
  <div>
    <el-button @click="getApis">调用api</el-button>
    <span>{{api}}</span>
    <router-view></router-view>
  </div>
</template>

<script>
import { getApis } from './service/service'
export default {
  name: 'App',
  data () {
    return {
      api: undefined
    }
  },
  methods: {
    getApis () {
      getApis().then(data => {
        console.log(data)
        this.api = data.data
      })
    }
  }
}
</script>

<style>
</style>



